































html, body {
  height: 100%;
  width: 100%;
  background: #A8CDF7;
  overflow: hidden;
}

#app {
  color: white;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.come-back {
  text-align: center;
  font-size: 2rem;
  line-height: 2;
  margin-top: 200px;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition-duration: .4s;
          transition-duration: .4s;
  font-family: Caslong, Tahoma, sans-serif;
}
.come-back.hidden {
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
}



